	padding:0;
}

/* With video Background */
.video_bg_show .body_wrap {
	position:relative;
}
.video_bg_show .video_bg {
	position:fixed;
	z-index:-1;
	top:0;
	left:0;
	width:100%;
	height:100%;
}
.video_bg_show .video_bg_overlay:after {
	content: ' ';
	position:absolute;
	left:0;
	top:0;
	bottom:0;
	right:0;
