}
.post_featured .post_nav_next {
	left:auto;
	right:0;
}
.post_featured .post_nav_item:before {
	position:absolute;
	display:block;
	top:50px;
	left: 25px;
	width:58px;
	height:58px;
	line-height:58px;
	font-size:25px;
	font-family: "fontello";
	border:1px solid #ddd;
	border-color:transparent;
