	opacity: 1;
	filter: alpha(opacity=100);
}
.post_featured .post_nav:hover .post_nav_prev:hover:before {
	opacity: 0;
	filter: alpha(opacity=0);
	left: -60px;
}
.post_featured .post_nav:hover .post_nav_next:hover:before {
	opacity: 0;
	filter: alpha(opacity=0);
	right: -60px;
}
