/* 11. Utils
-------------------------------------------------------------- */

/* Table of contents */
#toc {
	position:fixed;
	z-index:999;
	top: 0;
	bottom:0;
	right: 0;
	width: 60px;
}
#toc a { color: #ffffff; }
#toc.toc_no { display:none; }
#toc .toc_inner {
	position:absolute;
	top: 50%;
	right: -60px;
	-webkit-transform:translateY(-50%);
	   -moz-transform:translateY(-50%);
	        transform:translateY(-50%);
	-webkit-transition: all 0.3s ease-out;
	   -moz-transition: all 0.3s ease-out;
	    -ms-transition: all 0.3s ease-out;
	     -o-transition: all 0.3s ease-out;
	        transition: all 0.3s ease-out;
}
#toc.toc_fixed .toc_inner,
#toc:hover .toc_inner {
	right:0;
}
#toc .toc_item {
	position:relative;
	width:57px;
	height:60px;
	border-right:3px solid rgba(0,0,0,0.8);
	background-color:rgba(0,0,0,0.8);
}
#toc .toc_item.toc_separator {
	margin-bottom: 1px;
}
#toc .toc_item.current,
#toc .toc_item:hover {
	background-color:rgba(128,128,128,0.8);
}
#toc .toc_icon {
	position:absolute;
	display:block;
	z-index:2;
	top:0;
	left:0;
	width:57px;
	height: 60px;
	line-height:60px;
	text-align:center;
}
#toc .toc_icon.with_title {
	line-height:40px;
}
#toc .toc_icon:before {
	font-size:inherit;
	color:inherit;
	margin:0;
	padding:0;
}
#toc .toc_title {
	position:absolute;
	bottom:6px;
	left:0;
	display:block;
	width: 57px;
	line-height:12px;
	font-size:12px;
}
#toc .toc_description {
	position:absolute;
	top:0;
	right:-300px;
	width:240px;
	height:30px;
	padding: 1em;
	font-size: 0.875em;
	line-height:1.2em;
	z-index:1;
	background-color:rgba(128,128,128,0.8);
	color: #ffffff;
	opacity: 0;
	filter: alpha(opacity=0);
	-webkit-transition: all 0.3s ease-out;
	   -moz-transition: all 0.3s ease-out;
	    -ms-transition: all 0.3s ease-out;
	     -o-transition: all 0.3s ease-out;
	        transition: all 0.3s ease-out;
}
#toc .toc_item:hover .toc_description {
	right:68px;
	opacity: 1;
	filter: alpha(opacity=100);
